import { Component, h, Prop, Event, EventEmitter } from '@stencil/core';
import { IMenuItem } from '../../types/MenuItem';

@Component({
    tag: 'menu-button',
    shadow: true,
    styleUrl: 'menu-button.scss',
})
export class MenuButton {
    @Event() changeView: EventEmitter;
    @Event() closeMenu: EventEmitter;
    @Prop() menuItem: IMenuItem;

    constructor() {
        this.handleMenuClick = this.handleMenuClick.bind(this);
    }

    public render() {
        const { icon, label, view } = this.menuItem;

        return (
            <li class="ga-menu-item" onClick={() => this.handleMenuClick(view)}>
                <limel-icon
                    class="menu-icon"
                    name={icon}
                    size="small"
                ></limel-icon>
                <span>{label}</span>
            </li>
        );
    }

    private handleMenuClick(view) {
        this.changeView.emit(view);
        this.closeMenu.emit(false);
    }
}
